@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Sans";
  src: local("PT Sans"), url("../fonts/ptsans.woff") format("woff"), url("../fonts/ptsans.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "PT Sans";
  src: local("PT Sans Bold"), url("../fonts/ptsansbold.woff") format("woff"), url("../fonts/ptsansbold.woff2") format("woff2");
}
body {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  font-family: "PT Sans", "Arial", sans-serif;
  color: #000000;
  text-transform: uppercase;
  background-color: #ffffff;
}
a {
  text-decoration: none;
}
.container {
  width: 320px;
  min-width: 320px;
  margin: 0 auto;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}
@media (min-width: 768px) {
  .container {
    width: 768px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1200px;
    box-shadow: 0 5px 15px 0 rgba(0, 1, 1, 0.2);
  }
}
.button {
  display: block;
  font: inherit;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  vertical-align: middle;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  background-color: #81b3d2;
  border: none;
}
.button:hover,
.button:focus {
  background-color: #669ec0;
}
.button:active {
  color: rgba(0, 0, 0, 0.3);
  background-color: #5496bd;
}
.button:disabled {
  background-color: #e5e5e5;
}
.page-header {
  position: relative;
}
.page-header__wrapper {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}
.page-header__logo {
  z-index: 10;
  order: 1;
  margin: 0 auto;
}
.page-header__logo-image {
  display: block;
}
.page-header__button {
  display: none;
}
@media (max-width: 767px) {
  .page-header__button {
    position: absolute;
    top: 17px;
    right: 20px;
    display: block;
    width: 23px;
    height: 22px;
    background-color: transparent;
    border: none;
    border-radius: none;
    cursor: pointer;
  }
  .page-header__button::before,
  .page-header__button::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
  }
  .page-header__button--menu-opened::before,
  .page-header__button--menu-opened::after {
    width: 5px;
    height: 28px;
    margin-top: -14px;
    margin-left: -2px;
    background-color: #ffffff;
  }
  .page-header__button--menu-opened::before {
    transform: rotate(-45deg);
  }
  .page-header__button--menu-opened::after {
    transform: rotate(45deg);
  }
  .page-header__button--menu-closed::before,
  .page-header__button--menu-closed::after {
    width: 23px;
    height: 4px;
    margin-top: -2px;
    margin-left: -12px;
    background-color: #81b3d2;
    box-shadow: 0 8px 0 0 #81b3d2, 0 -8px 0 0 #81b3d2;
  }
  .page-header__button--nojs {
    display: none;
  }
}
@media (min-width: 768px) {
  .page-header__logo {
    position: absolute;
    top: 0;
    left: 50%;
    order: 0;
    transform: translateX(-50%);
  }
}
@media (min-width: 1200px) {
  .page-header__logo-image {
    width: 188px;
  }
}
