.reasons {
  margin-bottom: 23px;
}
.reasons__header {
  margin-bottom: 40px;
  padding: 0 30px;
}
.reasons__list {
  background-color: #eeeeee;
  margin: 0;
  padding: 0;
  list-style: none;
}
.reasons__item {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
  padding: 25px 35px;
  text-align: center;
}
.reasons__item:first-child::before {
  content: "";
  position: absolute;
  top: -37px;
  left: 0;
  z-index: 5;
  width: 100%;
  height: 37px;
  background-color: #81b3d2;
  background-image: url("../../img/bg-triangle.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100% auto;
}
.reasons__item::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 88%;
  height: 1px;
  background-color: rgba(0, 0, 0, 0.3);
  transform: translateX(-50%);
}
.reasons__item:last-child::after {
  display: none;
}
.reasons__item .reasons__item-title {
  margin: 0;
  margin-bottom: 10px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}
.reasons__item .reasons__number,
.reasons__item .reasons__detail {
  margin: 0;
  line-height: 21px;
  color: #333333;
}
.reasons__item .reasons__number {
  margin-bottom: 20px;
}
.reasons__item--with-image {
  padding: 0;
  background-color: #81b3d2;
}
