}
.reasons__item--with-image .reasons__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 40px 20px;
}
.reasons__item--with-image .reasons__image {
  display: block;
  width: 100%;
  height: 256px;
  margin: 0 auto;
  object-fit: cover;
}
.reasons__item--with-image .reasons__item-title {
  margin-bottom: 10px;
}
.reasons__item--with-image .reasons__number,
.reasons__item--with-image .reasons__detail {
  margin: 0;
  line-height: 21px;
}
